import React from 'react';

const Char = (props) => {
    const colorList = ['#FF7F50', '#556B2F', '#9400D3', '#FF00FF', '#7CFC00', '#8B008B', '#FF1493'];
    const style = {
        display: 'inline-block',
        padding: '16px',
        margin: '16px',
        border: props.character !== ' ' ? '1px solid black' : 'none',
        textAlign: 'center',
        color: colorList[Math.floor(Math.random() * colorList.length)]
    };

    return (
        <div 
            style={style}
            onClick={props.clicked}
        >
            {props.character}
        </div>
    )
}

export default Char;