import React from 'react';

const ArticleTitleValidation = (props) => {
    let message = '';

    if (props.text.length === 0) {
        message = 'Please add a text in the input!';
    } else if (props.text.charAt(0) !== props.text.charAt(0).toUpperCase()) {
        message = 'First letter needs to be uppercase!';
    } else if (props.text.length < 5) {
        message = 'The text is too short!';
    } else if (props.text.length > 10) {
        message = 'The text is too long!';
    }

    return (
        <h3 className="error-message">{message}</h3>
    )
}

export default ArticleTitleValidation;