import React, { useState } from 'react';
import Button from '../Button/Button.js';
import ArticleTitleValidation from '../ArticleTitleValidation/ArticleTitleValidation.js';
import Char from '../Char/Char.js';

const Article = (props) => {
    const [title, setTitle] = useState(props.title);

    const onTitleInputChange = (event) => {
        setTitle(event.target.value);
    }

    const deleteCharHandler = (index) => {
        const text = title.split('');
        text.splice(index, 1);
        const updatedText = text.join('');
        setTitle(updatedText);
    }

    return (
        <div className="card full-width">
            <h1 
                onClick={props.titleClick} 
                style={{ color: props.color }}>
                {title.split('').map((letter, index) => 
                    <Char 
                        key={letter + index}
                        character={letter} 
                        clicked={() => deleteCharHandler(index)}
                    />
                )}
            </h1>

            <p>{props.children}</p>

            <p><strong>You can change the article title here:</strong></p>

            <input type="text" value={title} onChange={onTitleInputChange} />

            <ArticleTitleValidation text={title} />

            <Button 
                click={props.deleteHandler}
                text="Delete article"
            />
        </div>
    );
}
  
export default Article;
  