import React, { useState } from 'react';
import Article from '../Article/Article';
import Button from '../Button/Button';
import articlesListData from '../../data/articles-list-data';

const ArticlesList = () => {
    const [titleColor, setTitleColor] = useState('black');
    const [articles, setArticles] = useState(articlesListData);
    
    const changeTitleColor = (color) => {
        setTitleColor(color);
    }

    const deleteArticleHandler = (articleIndex) => {
        // v1
        // articles.splice(articleIndex, 1);
        // setArticles([...articles]); // Whe changed the array values but it's still the same array and React doesn't see any reasons to re-render because state
                                    // hasn't changed, the new array is the old array. One easy way to avoid this is by spreading the array into a new array
                                    // using the "..." operator

        // v2
        // const newArticles = articles.slice(); // slice without arguments creates a new copy of the array 
        // newArticles.splice(articleIndex, 1);
        // setArticles(newArticles);

        // v3
        const newArticlesList = [...articles];   // spread operator - create a new array from another array
        newArticlesList.splice(articleIndex, 1); // we need to create a copy of the old array, change that and then update the state with the new array (after we made
        setArticles(newArticlesList);            // also the changes in the new array)
        
    }

    return (
        <div className="articles">
            <Button 
                click={() => changeTitleColor('red')}
                text="Change color to red"
            />

            {
                articles.map((article, index) => (
                    <Article
                        key={article.id}
                        title={article.title}
                        color={titleColor}
                        titleClick={() => changeTitleColor(article.color)}
                        deleteHandler={() => deleteArticleHandler(index)}
                    >
                        {article.text}
                    </Article>
                ))
            }   
        </div>
    );
}
  
export default ArticlesList;